@use "base/variables" as var;
@use "base/mixins" as mix;

.formulario{
    display: flex;
    flex-direction: column;

    &__fieldset{
        border: none;
        margin-top: 1rem;
        padding: 0;
    }

    &__legend{
        color: var.$gris;
        font-size: 2.2rem;
        margin-bottom: 2rem;
    }

    &__campo{
        display: flex;
        flex-direction: column;
        gap: 1rem;
        margin: 1.5rem 0;
    }

    &__contenedor-icono{
        display: flex;
        border-radius: .5rem;
        overflow: hidden;
    }

    &__icono{
        background-color: var.$gris-oscuro;
        width: 4rem;
        display: flex;
        align-items: center;
        justify-content: center;

        i{
            color: var.$blanco;
            font-size: 2rem;
        }
    }

    &__label{
        display: block;
        font-size: 1.8rem;
        color: rgba(var.$negro, .8);
    }

    &__input,
    &__select{
        padding: 1rem;
        border: 1px solid darken(var.$gris-claro, 20%);
        border-radius: .5rem;

        &--file{
            border: none;
        }

        &--sociales{
            @extend .formulario__input;
            flex: 1;
            border-top-left-radius: 0;
            border-bottom-left-radius: 0;
        }
    }

    &__radio{
        display: flex;
        gap: 2rem;
    }

    &__texto{
        margin-top: 3rem;
    }

    &__imagen{
        max-width: 20rem;
    }

    &__listado{
        @include mix.tags;
    }

    &__submit{
        @include mix.boton(var.$azul, var.$blanco);
        width: auto;
        align-self: flex-start;

        &--registrar,
        &--full{
            width: 100%;
        }
    }
}