@use "base/mixins" as mix;
@use "base/variables" as var;

.barra{
    background-color: var.$azul;

    &__contenido{
        @include mix.contenedor;
        display: flex;
        flex-direction: column;

        @include mix.desktop {
            flex-direction: row;
            justify-content: space-between;
            align-items: center;
        }
    }

    &__logo{
        @include mix.logo;
        @include mix.texto-fluido(2rem, 3rem);
    }
}