// Como lo mejore con la opcion Dinamica de elegir Elementos Por Pagina

// Utlizando JavaScript leemos la opcion del usuario, la cargamos en la URL, desde PHP la leemos con $_GET y hacemos la consulta con la cantidad de elementos a mostrar en la BD. Tener encuenta que el usuario puede cambiar el valor de la URL, fijar la seleccion del usuario en el <select> y que se mantenga en las diferentes paginas

.paginacion{
    margin-top: 5rem;
    margin-bottom: 3rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 3rem;

    @include mix.tablet {
        flex-direction: row;
    }

    &__seleccion{
        display: flex;
        gap: 1rem;
        justify-content: center;
        align-items: center;

        @include mix.tablet {
            justify-content: flex-start;
        }
    }

    &__enlaces{
        display: flex;
        gap: 1rem;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;

        @include mix.tablet {
            justify-content: flex-end;
        }
    }

    &__option,
    &__select,
    &__texto{
        color: var.$azul;
        font-size: 1.6rem;
        font-weight: var.$bold;
    }

    &__select{
        border: 1px solid var.$azul;
        border-radius: .5rem;
        color: var.$gris-oscuro;
        padding: 1rem .3rem;
    }

    &__enlace{
        color: var.$azul;
        font-size: 1.6rem;

        &--texto{
            padding: 1rem;
            border-radius: .5rem;
            border: 1px solid var.$azul;

            &:hover{
                background-color: var.$azul;
                color: var.$blanco;
            }
        }

        &--numero,
        &--actual{
            font-weight: var.$bold;
            display: none;

            @include mix.tablet {
                display: inline;
            }
        }

        &--actual{
            color: var.$gris;
        }
    }
}
