@use "base/mixins" as mix;
@use "base/variables" as var;

.eventos-registro{
    @include mix.contenedor;
    @include mix.tablet {
        display: flex;
        align-items: flex-start;
        gap: 2rem; 
    }

    &__listado{
        @include mix.tablet {
            flex: 0 0 60%;
        }
    }

    &__grid{
        @include mix.grid(1, 3rem);
        @include mix.desktop {
            @include mix.grid(2, 3rem);
        }
    }

    &__heading{

        &--conferencias,
        &--workshops{
            white-space: nowrap;
            @include mix.texto-fluido(2rem, 5rem);
        }

        &--conferencias{
            color: var.$azul;
            
        }

        &--workshops{
            color: var.$secundario;
            margin-top: 6rem;
        }
    }

    &__fecha{
        margin: 6rem 0 3rem 0;
    }
}

.registro{
    margin-top: 3rem;

    &.registro-aside{
        background-color: var.$gris-claro;
        padding: 3rem;
        border-radius: 1rem;
    
        @include mix.tablet {
            flex: 0 0 40%;
            position: -webkit-sticky;
            position: sticky;
            top: 3rem;
        }
    }
    

    &__heading{
        margin: 0 0 3rem 0;
    }

    &__evento{
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 1rem;

        &:nth-child(even){
            background-color: darken(var.$gris-claro, 3%);
        }
    }

    &__nombre{
        font-size: 2rem;
        font-weight: var.$bold;
        margin: 0;
    }

    &__eliminar{
        all: unset;
        color: var.$rojo;

        &:hover{
            cursor: pointer;
        }
    }

    &__texto{
        text-align: center;
    }

    &__regalo{
        margin-top: 3rem;
    }

    &__label{
        display: block;
        font-size: 2.2rem;
        font-weight: var.$bold;
        margin-bottom: 1rem;
    }

    &__select{
        width: 100%;
        background-color: var.$blanco;
        border: 1px solid var.$gris;
        padding: 1rem;
        border-radius: .5rem;
    }
}