@use "base/mixins" as mix;
@use "base/variables" as var;

.horas{
    @include mix.reset-lista;
    @include mix.grid(1, 1rem);
    @include mix.tablet {
        @include mix.grid(2, 1rem)
    }

    &__hora{
        color: var.$azul;
        border: 1px solid var.$azul;
        border-radius: 1rem;
        padding: 1rem;
        text-align: center;

        &:hover{
            cursor: pointer;
        }

        &--seleccionada{
            background-color: var.$azul;
            color: var.$blanco;
        }

        &--deshabilitada{
            border: 1px solid var.$gris;
            color: var.$gris;
            opacity: .5;

            &:hover{
                cursor: not-allowed;
            }
            
        }
    }
}