@use "base/mixins" as mix;
@use "base/variables" as var;

.bloques{
    @include mix.contenedor;
    @include mix.tablet {
        margin-top: 10rem;
    }

    &__grid{
        @include mix.grid(1, 3rem);
        @include mix.desktop {
            @include mix.grid(2, 3rem);
        }
    }
}

.bloque{
    padding: 3rem;
    color: var.$blanco;
    border-radius: 1rem;
    
    &:nth-child(1){
        @include mix.bg-gradient(#F97316);
    }

    &:nth-child(2){
        @include mix.bg-gradient(#A855F7);
    }

    &:nth-child(3){
        @include mix.bg-gradient(#59910B);
    }

    &:nth-child(4){
        @include mix.bg-gradient(#17769E);
    }

    &__heading{
        margin: 1rem 0 2rem 0;
        font-size: 3.6rem;
    }

    &__texto{
        margin: 1rem 0;

        &--cantidad{
            @extend .bloque__texto;
            margin-top: 5rem;
            font-weight: var.$black;
            font-size: 6rem;
            text-align: center;
        }
    }
}