@use "base/mixins" as mix;
@use "base/variables" as var;

.resumen{
    background-image: linear-gradient(to right, rgba(var.$azul, .7), rgba(var.$azul, .9)), url(../img/bg_numeros.jpg);
    background-image: linear-gradient(to right, rgba(var.$azul, .7), rgba(var.$azul, .9)), image-set(
        url(../img/bg_numeros.avif) 1x,
        url(../img/bg_numeros.webp) 1x,
        url(../img/bg_numeros.jpg) 1x
    );
    background-size: cover;
    background-position: center;

    padding: 12rem 0 ;

    &__grid{
        @include mix.contenedor();
        @include mix.grid(1, 5rem);
        @include mix.tablet {
            @include mix.grid(2, 5rem);
        }
    }

    &__bloque{
        text-align: center;
        color: var.$blanco;
    }

    &__texto{
        font-weight: var.$black;
        line-height: 1;
        margin: 0;
        font-size: 2.4rem;
        text-transform: uppercase;

        &--numero{
            font-size: 8rem;
        }
    }
}