@use "base/mixins" as mix;
@use "base/variables" as var;

.listado-ponentes{
    @include mix.reset-lista;
    @include mix.grid(1, 1rem);

    &__ponente{
        color: var.$azul;
        border: 1px solid var.$azul;
        padding: 1rem;
        border-radius: 1rem;
        text-align: center;

        &:hover{
            cursor: pointer;
        }

        &--seleccionado{
            background-color: var.$azul;
            color: var.$blanco;
        }
    }
}