@use "base/mixins" as mix;
@use "base/variables" as var;

.dashboard{
    height: 100%;
    display: flex;
    flex-direction: column;
    background-color: var.$gris-claro;

    &__header{
        background-color: var.$gris-oscuro;
        padding: 2rem;
    }

    &__header-grid{
        display: flex;
        flex-direction: column;
        align-items: center;

        @include mix.tablet {
            flex-direction: row;
            justify-content: space-between;
        }
    }

    &__logo{
        @include mix.logo;
        @include mix.texto-fluido(2rem, 3rem);
    }
/*
    &__nav{

    }

    &__form{

    }
*/
    &__submit--logout{
        @include mix.enlace;
    }

    &__grid{
        display: flex;
        flex: 1;
        overflow-y: auto;
    }

    &__sidebar{
        background-color: lighten(var.$gris-oscuro, 10%);
        width: 12rem;
    }

    &__menu{
        padding: 1rem;
        display: flex;
        flex-direction: column;
        gap: 1rem;
    }

    &__enlace{
        color: lighten(var.$gris-oscuro, 40%);
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 2rem;
        transition: 300ms background-color ease-in-out, 300ms color ease, 300ms border-radius ease;

        &:hover,
        &--actual{
            background-color: lighten(var.$gris-oscuro, 20%);
            color: var.$blanco;
            border-radius: 1rem;
        }
    }

    &__icono{
        font-size: 2rem;
    }

    &__menu-texto{
        font-size: 1.1rem;
        text-transform: uppercase;
        font-weight: var.$bold;
        margin-top: .5rem;
    }

    &__contenido{
        width: 100%;
        padding: 1rem 1rem 3rem 1rem;
        overflow-y: scroll;

        @include mix.tablet {
            padding: 1rem 5rem 3rem 5rem;
        }
    }

    &__contenedor-boton{
        @include mix.tablet {
            display: flex;
            justify-content: flex-end;
            margin-bottom: 5rem;
        }
    }

    &__boton{
        @include mix.boton(var.$azul, var.$blanco);
        text-transform: uppercase;
        font-size: 1.4rem;
        
        @include mix.tablet {
            width: auto;
        }
    }

    &__contenedor{
        @include mix.shadow;
        background-color: var.$blanco;
        padding: 2rem;
        border-radius: 1rem;
    }

    &__formulario{
        @extend .dashboard__contenedor;
        @include mix.contenedor-md;
    }

    &__grafica{
        @include mix.contenedor-sm;
    }
}