@use "base/mixins" as mix;
@use "base/variables" as var;

.navegacion{
    display: flex;
    flex-direction: column;

    @include mix.desktop {
        flex-direction: row;
    }

    &__enlace{
        color: var.$blanco;
        font-size: 2rem;
        padding: 2rem;
        font-weight: var.$black;
        text-transform: uppercase;
        text-align: center;
        
        // Alinea verticalmente cada enlace de la barra
        @include mix.desktop {
            display: flex;
            align-items: center;
        }

        &:hover,
        &--actual{
            background-color: var.$blanco;
            color: var.$azul;
        }
    }
}