@use "base/mixins" as mix;
@use "base/variables" as var;

.devwebcamp{

    &__grid{
        @include mix.contenedor;
        @include mix.grid(1, 3rem);

        @include mix.tablet {
            @include mix.grid(2, 5rem);
            align-items: center;
        }
    }

    &__texto{
        line-height: 2;
    }
}