@use "base/mixins" as mix;
@use "base/variables" as var;

.boletos{
    &__grid{
        @include mix.contenedor-sm;
        @include mix.grid(1, 5rem);
    }
}

.boleto{
    width: min(100% - 5rem, 65rem);
    margin: 0 auto;
    padding: 2rem;
    border-radius: 2rem;
    color: var.$blanco;
    position: relative;

    @include mix.tablet {
        padding: 6rem;
    }

    &::before,
    &::after{
        position: absolute;
        content: "";
        background-color: var.$blanco;
        width: 5rem;
        height: 5rem;
        top: calc(50% - 2.5rem);
    }

    &::before{
        clip-path: circle(50% at 25% 50%);
        left: 0;
    }

    &::after{
        clip-path: circle(50% at 75% 50%);
        right: 0;
    }

    &--presencial{
        background-color:hsla(178,100%,39%,1);
        background-image:
        radial-gradient(at 63% 56%, hsla(209,100%,47%,1) 0px, transparent 50%),
        radial-gradient(at 100% 100%, hsla(178,100%,39%,1) 0px, transparent 50%),
        radial-gradient(at 39% 43%, hsla(209,100%,47%,1) 0px, transparent 50%),
        radial-gradient(at 19% 26%, hsla(178,100%,39%,1) 0px, transparent 50%),
        radial-gradient(at 15% 82%, hsla(209,100%,47%,1) 0px, transparent 50%),
        radial-gradient(at 78% 78%, hsla(178,100%,39%,1) 0px, transparent 50%),
        radial-gradient(at 76% 27%, hsla(209,100%,47%,1) 0px, transparent 50%);
    }

    &--virtual{
        background-color: var.$secundario;
    }

    &--gratis{
        background-color: var.$azul;
    }

    &__logo{
        @include mix.logo;
        @include mix.texto-fluido(1rem, 4rem);
    }

    &__plan{
        font-size: 2rem;
        text-transform: uppercase;
        margin: 3rem 0 0 0;
    }

    &__precio{
        margin: 3rem 0 0 0;
        font-weight: var.$black;
        font-size: 4rem;
    }

    &__enlace-contenedor{
        margin-top: 5rem;
        display: flex;
        justify-content: center;
    }

    &__enlace{
        @include mix.boton(var.$azul, var.$blanco);
        width: auto;
    }

    $modificador: &;

    &--acceso{
        width: 30rem;
        height: 50rem;
        padding: 10rem 3rem;

        @include mix.tablet {
            width: 60rem;
            height: 30rem;
            padding: 6rem;
            display: flex;
            flex-direction: column;
            justify-content: center  
        }

        #{$modificador}__logo{
            @include mix.texto-fluido(2rem, 3rem);
            margin: 0;
            text-align: left;
        }

        #{$modificador}__nombre{
            font-size: 2.4rem;
            text-transform: uppercase;
            font-weight: var.$black;
        }

        #{$modificador}__codigo{
            font-size: 4rem;
            font-weight: var.$bold;
            text-align: center;
            position: absolute;
            bottom: 7rem;
            margin: 0;
            left: 0;
            width: 100%;

            @include mix.tablet {
                rotate: 270deg;
                transform-origin: 0 0;
                top: 100%;
                left: calc(100% - 10rem);
                width: 30rem;
            }
        }
    }

    &::before{
        clip-path: circle(50% at 50% 25%);
        left: calc(50% - 2.5rem);
        top: 0;

        @include mix.tablet {
            clip-path: circle(50% at 25% 50%);
            left: 0;
            top: calc(50% - 2.5rem)
        }
    }

    &::after{
        clip-path: circle(50% at 50% 75%);
        left: calc(50% - 2.5rem);
        top: calc(100% - 5rem);

        @include mix.tablet {
            clip-path: circle(50% at 75% 50%);
            left: calc(100% - 5rem);
            top: calc(50% - 2.5rem);
        }
    }
}