@use "base/mixins" as mix;
@use "base/variables" as var;

.paquetes{

    &__grid{
        @include mix.contenedor;
        @include mix.grid(1, 3rem);

        @include mix.desktop {
            @include mix.grid(3, 3rem);
            align-items: flex-start
        }
    }

    &__submit{
        @include mix.boton(var.$azul, var.$blanco);
        margin-top: 3rem;
    }
}

.paquete{
    @include mix.card;

    &:nth-child(2),
    &:nth-child(3){
        order: -1;

        @include mix.desktop {
            order: unset;
        }
    }

    &__lista{
        @include mix.reset-lista;
        line-height: 2;
    }

    &__precio{
        font-size: 6rem;
        margin: 3rem 0;
        color: var.$azul;
        font-weight: var.$black;
        text-align: center;
    }
}